.align-label{
    display: inline-block;
    width: 250px;
    font-size: 22px;
}
.center{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 12px auto;
    width: 724px;
}

.align-input{
    font-size: 22px;
    width: 300px;
}

.checkbox{
    display: flex;
    flex-direction: row;
    margin-left: 15px;
    box-sizing: border-box;
    /* border: 1px solid red; */
    height: 80px;
    width: 900px;
}
.checkboxes{
    box-sizing: border-box;
    /* border: 1px solid blue; */
}
.space{
    margin:15px;
}
.font{
    font-size: 22px;
}

.button{
    background-color: black;
    padding:5px 30px 5px 30px;
    font-size: 22px;
    color: white;
}

.nav-button{
    background-color: black;
    padding:10px 30px 10px 30px;
    font-size: 32px;
    color: white;
}

.wrapper{
    height: 100vh;
    box-sizing: border-box;
  }
  
  .header{
    height: 10%;
    margin-top: 15px;
    border-bottom: 2px solid black;
    border-top: 2px solid black;
    box-sizing: border-box;
  }
 

  .header h1{
    font-size: 40px;
    margin: 25px 120px;
  }
  .container{
    display: flex;
    height: 90%;
    /* border: 2px solid red; */
    box-sizing: border-box;
    flex-direction: row;
  }
  
  .sidebar{
    width: 20%;
    padding: 20px;
    border-right: 2px solid black;
    line-height: 2.5;
    box-sizing: content-box;
    text-decoration: none
  }
  
  .side-titles{
    color: white;
    font-weight: 800;
    /* border: 1px solid blue; */
    font-size: 19px;
    justify-content: center;
    text-decoration: none;
    height: 49px;
    width: 196px;
    display: flex;
    box-sizing: border-box;
    margin: 2px auto;
    padding: 0px;
    border-radius: 25px;
    background-color: rgb(0, 0, 0);
}

  .main-content{
    width: 80%;
    padding: 12px 0 0 40px;
    /* border: 2px solid red; */
  }
 
  .table-border{
    margin-top: 24px;
    border: 2px solid black;
    border-collapse: collapse;
    text-align: center;
    font-size: 22px;
    padding: 4px;
  }
  
  .t-width{
    width: 100%;
  }
  
  .profile-pic{
    border-radius: 75px;
      width: 60px;
  }